import './corgi.entry.scss';

const vscode = (window as any)['acquireVsCodeApi']();

function spawnHelper(images: string[]) {
  const container = document.getElementsByClassName('corgi-mode-container')[0];
  const newHelper = new Image();
  newHelper.classList.add('helper');

  // Choose a random helper.
  newHelper.src = images[Math.floor(Math.random() * images.length)];

  if (Math.random() < 0.5) {
    newHelper.classList.add('ltor-helper');
  } else {
    newHelper.classList.add('rtol-helper');
  }

  newHelper.addEventListener('animationend', () => {
    newHelper.parentElement?.removeChild(newHelper);
  });

  container?.appendChild(newHelper);
}

// Handle messages sent from the extension to the webview
window.addEventListener('message', event => {
  const message = event.data;
  if (message.command === 'spawn') {
    spawnHelper(message.data.helperImages);
  }
});

// Set up iframe for the previewserver
document.querySelector('#root')!.innerHTML = `<div class="corgi-mode-container"></div>`;
